@import "../helpers/all";

.header {
   $parent: &;
   padding: 10px 0 20px;
   &__container {
      display: flex;
      justify-content: space-between;
      align-items: flex-start; }
   &__logo {}
   &__nav {
      @include sm {
         display: flex;
         flex-direction: column;
         justify-content: center;
         position: absolute;
         top: 60px;
         left: 0;
         width: 100%;
         height: 100vh;
         z-index: 10;
         overflow: auto;
         opacity: 0;
         border: 0;
         padding: 0 0 60px;
         visibility: hidden;
         transform: translate3d(0, 100px, 0);
         transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
         background: $color-3;
         text-align: center; }
      &.is-active {
         opacity: 1;
         visibility: visible;
         transform: translate3d(0, 0, 0);
         transition: transform .4s ease, opacity .4s ease, visibility 0s 0s, -webkit-transform .4s ease; }
      &-list {
         @include clr;
         display: flex;
         @include sm {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            overflow: auto; }
         li {
            float: left;
            &:not(:last-child) {
               margin-right: 20px; }
            &.is-active a {
               border-bottom-color: $color-3; }
            a {
               font-size: 16px;
               line-height: 17px;
               color: #4d586d;
               border-bottom: 1px solid transparent;
               @include trs;
               @include sm {
                  color: $white;
                  text-transform: uppercase;
                  font-size: 16px; }
               &:hover {
                  color: $color-3; } } } } }

   &__nav-btn {
      $parent: &;
      display: none;
      border: 0;
      outline: 0;
      padding: 0;
      margin-left: 30px;
      z-index: 11;
      @include sm {
         display: block; }

      &.is-active span {
         color: $color-1; }

      &-burger {
         margin: auto;
         width: 40px;
         height: 48px;
         cursor: pointer; }
      &-lines {
         width: 40px;
         height: 3px;
         &:after, &:before {
            width: 40px;
            height: 3px; }
         position: relative;
         transform: translateY(25px);
         background: white;
         transition: all .3s;
         #{$parent}.is-active & {
            background: rgba(255, 255, 255, 0); }
         &:before {
            content: "";
            position: absolute;
            left: 0;
            bottom: 10px;
            background: white;
            transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
         &:after {
            content: "";
            position: absolute;
            left: 0;
            top: 10px;
            background: white;
            transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
         #{$parent}.is-active & {
            &:after {
               top: 0;
               transform: rotate(45deg);
               transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); }
            &:before {
               bottom: 0;
               transform: rotate(-45deg);
               transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); } } } }
   &__tel {
      @include f('ms900', 18, 18);
      color: $color-3; } }
