/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

body {
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "MuseoSansCyrl-300", Arial, sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 23px;
  color: #58595b;
}

body.is-locked {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 0;
  overflow: hidden;
}

ul li::before {
  margin-right: 3px;
  content: "•";
  color: currentColor;
}

.container {
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 15px;
}

.container:after {
  content: "";
  display: table;
  clear: both;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

@media only screen and (max-width: 1199px) {
  .row {
    justify-content: center;
    margin: 0 -10px;
  }
}

@media only screen and (max-width: 767px) {
  .row {
    flex-direction: column;
  }
}

.main {
  overflow: hidden;
}

.title {
  font-family: "MuseoSansCyrl-900", Arial, sans-serif;
  font-weight: normal;
  color: #343c53;
}

.title_default {
  font-size: 30px;
  line-height: 36px;
}

.btn {
  display: inline-block;
  padding: 17px 64px 15px;
  outline: 0;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.25s ease-out;
  font-family: "MuseoSansCyrl-700", Arial, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  border-radius: 5px;
}

.btn_lime {
  color: #000;
  background-color: #6cff2b;
}

.btn_lime:hover {
  background-color: #5bc600;
}

.btn_orange {
  color: #fff;
  background-color: #ff6101;
}

.btn_orange:hover {
  background-color: #ff4c01;
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  margin-right: 5px;
  width: 31px;
  height: 35px;
}

.logo span {
  font-family: "Times New Roman", sans-serif;
  font-weight: 700;
  font-size: 18px;
  color: #4c4c4c;
}

