.header {
  padding: 10px 0 20px;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

@media only screen and (max-width: 767px) {
  .header__nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    overflow: auto;
    opacity: 0;
    border: 0;
    padding: 0 0 60px;
    visibility: hidden;
    transform: translate3d(0, 100px, 0);
    transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
    background: #ff6101;
    text-align: center;
  }
}

.header__nav.is-active {
  opacity: 1;
  visibility: visible;
  transform: translate3d(0, 0, 0);
  transition: transform .4s ease, opacity .4s ease, visibility 0s 0s, -webkit-transform .4s ease;
}

.header__nav-list {
  display: flex;
}

.header__nav-list:after {
  content: "";
  display: table;
  clear: both;
}

@media only screen and (max-width: 767px) {
  .header__nav-list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: auto;
  }
}

.header__nav-list li {
  float: left;
}

.header__nav-list li:not(:last-child) {
  margin-right: 20px;
}

.header__nav-list li.is-active a {
  border-bottom-color: #ff6101;
}

.header__nav-list li a {
  font-size: 16px;
  line-height: 17px;
  color: #4d586d;
  border-bottom: 1px solid transparent;
  transition: all 0.25s ease-out;
}

@media only screen and (max-width: 767px) {
  .header__nav-list li a {
    color: #fff;
    text-transform: uppercase;
    font-size: 16px;
  }
}

.header__nav-list li a:hover {
  color: #ff6101;
}

.header__nav-btn {
  display: none;
  border: 0;
  outline: 0;
  padding: 0;
  margin-left: 30px;
  z-index: 11;
}

@media only screen and (max-width: 767px) {
  .header__nav-btn {
    display: block;
  }
}

.header__nav-btn.is-active span {
  color: #58595b;
}

.header__nav-btn-burger {
  margin: auto;
  width: 40px;
  height: 48px;
  cursor: pointer;
}

.header__nav-btn-lines {
  width: 40px;
  height: 3px;
  position: relative;
