  transform: translate3d(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.8);
  transition: opacity .3s ease, transform .5s ease, visibility .5s;
  will-change: opacity, transform;
}

.popup:after {
  content: '';
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.popup.opened {
  opacity: 1;
