  width: 90px;
  background: #ff6101;
  content: '';
}

.prices__card-title {
  margin-bottom: 10px;
  font-size: 30px;
  line-height: 36px;
  color: #ff6101;
}

.prices__card-price {
  font-size: 26px;
}

.prices__card-price span {
  font-family: "MuseoSansCyrl-900", Arial, sans-serif;
  font-weight: normal;
  font-size: 30px;
  line-height: 30px;
}

.prices__card-list {
  font-size: 15px;
  line-height: 21px;
}

.prices__card-list_orange {
  color: #ff6101;
}

.prices__card-btn {
  min-width: 190px;
  padding: 17px 20px 15px;
}

.prices__card-actions {
  margin-top: 30px;
  text-align: center;
}

.prices__card-actions-text {
  margin-top: 10px;
  font-size: 14px;
  line-height: 16px;
}

.prices__guarantee {
  margin: 40px 0 0 40px;
  font-size: 16px;
  color: #fff;
}

.prices__guarantee-title {
  margin-bottom: 10px;
  font-size: 30px;
  line-height: 36px;
  color: #ff6101;
}

.testimonials {
  padding: 50px 0;
}

.testimonials__title {
  margin-bottom: 40px;
}

.testimonials__item {
  position: relative;
  flex: 0 1 50%;
  padding: 0 15px;
  font-size: 16px;
  line-height: 21px;
}

.testimonials__item:nth-child(2) ~ .testimonials__item {
  padding: 30px 15px 0;
}

.testimonials__item-inner {
  padding-left: 20px;
}

.testimonials__item::before {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 3px;
  background: #343c53;
  content: '';
}

.testimonials__item-text {
  margin-bottom: 20px;
  color: #343c53;
  font-family: "MuseoSansCyrl-300Italic", Arial, sans-serif;
  font-weight: normal;
}

.testimonials__item-person {
  color: #000;
}

.testimonials__item-person-name {
  font-family: "MuseoSansCyrl-900", Arial, sans-serif;
  font-weight: normal;
  font-size: 17px;
  line-height: 23px;
}

.faq {
  padding: 50px 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.faq__title {
  margin-bottom: 40px;
}

.faq__item:not(:last-child) {
  margin-bottom: 30px;
}

.faq__question {
  position: relative;
  padding-bottom: 2px;
  display: inline-block;
  vertical-align: top;
  font-size: 20px;
  line-height: 30px;
}

.faq__question::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  transition: .3s ease;
  background-image: linear-gradient(90deg, currentColor, currentColor 75%, transparent 75%, transparent 100%);
  background-size: 20px 1px;
}

.faq__answer {
  font-size: 16px;
}

.clients {
  padding: 50px 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.clients__title {
  margin-bottom: 30px;
  color: #fff;
  text-align: center;
}

.clients__row {
  justify-content: space-between;
}

.clients__row:not(:last-child) {
  margin-bottom: 30px;
}

.clients__item {
  flex: 0 0 auto;
  padding: 0 15px;
  text-align: center;
}

.clients__item a {
  display: inline-block;
  transition: all 0.25s ease-out;
}

.clients__item a:hover {
  opacity: .75;
}
